import React from "react";
import { Box, Typography, Paper } from "@mui/material";

const Experience = ({ mode, experience }) => {
  console.log(experience);
  return (
    <Paper
      elevation={12}
      sx={{
        display: "flex",
        flexDirection: "column",
        justifyContent: "start",
        width: "40%",
        backgroundColor: "#17141D",
        padding: "50px",
        borderRadius: "20px",
        gap: "20px",
        "&:hover": {
          boxShadow: 24,
        },
      }}
    >
      <Box sx={{ width: "100%", display: "flex", gap: "20px" }}>
        <img
          style={{ height: "50px", width: "50px", borderRadius: "50%" }}
          src={experience.logo}
        />
        <Box>
          <Typography
            sx={{
              fontSize: { xs: "15px", sm: "15px", md: "15px", lg: "20px" },
              color: mode === "dark" ? "#FFFFFF" : "#575380",
            }}
            fontFamily={"sans-serif"}
          >
            {experience.title}
          </Typography>
          <Typography
            sx={{
              fontSize: { xs: "15px", sm: "15px", md: "15px", lg: "20px" },
              color: mode === "dark" ? "#A9A3C1" : "#575380",
            }}
            fontFamily={"sans-serif"}
          >
            {experience.timePeriod}
          </Typography>
        </Box>
      </Box>
      <Box>
        {experience.description.map((desc) => {
          return (
            <Typography
              sx={{
                fontSize: { xs: "15px", sm: "15px", md: "15px", lg: "20px" },
                color: mode === "dark" ? "#A9A3C1" : "#575380",
              }}
              fontFamily={"sans-serif"}
            >
              • {desc}
            </Typography>
          );
        })}
      </Box>
    </Paper>
  );
};

const Professional = ({ mode, experiences }) => {
  return (
    <Box
      sx={{
        display: "flex",
        flexDirection: "column",
        alignItems: "center",
        justifyContent: "center",
        gap: "50px",
      }}
    >
      <Box
        sx={{
          display: "flex",
          alignItems: "space-around",
          justifyContent: "center",
          gap: "10%",
        }}
      >
        {experiences.map((experience) => {
          return <Experience mode={mode} experience={experience} />;
        })}
      </Box>
    </Box>
  );
};

export default Professional;
